export default {
	data() {
		return {
			cover:""
		}
	},
	onLoad(params) {
		this.cover = params.cover
		uni.setNavigationBarColor({
			frontColor:"#ffffff",
			backgroundColor:"#000000"
		})
		
	},
	methods: {
		operator(){
			var _this = this;

			uni.showActionSheet({
				itemList:['保存图片到本地'],
				success: (res) => {
					if(res.tapIndex == 0){
						//console.log('下载')
						uni.showLoading({
							title:"图片保存中"
						})
						uni.downloadFile({
							url:_this.cover,
							success: (result) => {
								//console.log(result.tempFilePath);
								uni.saveImageToPhotosAlbum({
									filePath:result.tempFilePath,
									success: () => {
										uni.showToast({
											title:'保存成功',
											duration:2000
										})
									},
									complete: () => {
										uni.hideLoading()
									}
								})
							}
						})
					}
				}
			})

		}
	}
}
